import React, { useState, useEffect } from 'react';
import { useUser } from '../../../context/UserContext';
import './BlockedModal.css';

const BlockedModal = ({ onClose }) => {
  const { username } = useUser();
  const [isVisible, setIsVisible] = useState(true);

  const handleClose = () => {
    setIsVisible(false);
    setTimeout(() => {
      if (onClose) onClose();
    }, 300);
  };

  const handleLinkClick = (e) => {
    e.preventDefault();
    const url = e.currentTarget.href;
    
    if (window.electronAPI && window.electronAPI.openExternal) {
      window.electronAPI.openExternal(url);
    } else {
      window.open(url, '_blank', 'noopener,noreferrer');
    }
  };
  
  return (
    <div className={`blocked-modal-overlay ${isVisible ? 'visible' : 'hidden'}`}>
      <div className="blocked-modal">
        <div className="blocked-modal-content">
          <h2>Аккаунт заблокирован</h2>
          <p>
            Ваш аккаунт <span className="username">{username}</span> был заблокирован.
          </p>
          <p>
            Для получения дополнительной информации обратитесь к <a href="https://discord.gg/MPfPsXabjK" className="ds-link" onClick={handleLinkClick}>администрации</a>.
          </p>
          <button onClick={() => handleLinkClick({ currentTarget: { href: 'https://vk.com/market/product/razban-201145305-9691853' } })} className="unban-button">
            Купить разбан
          </button>
          <button onClick={handleClose} className="close-button">
            Закрыть
          </button>
        </div>
      </div>
    </div>
  );
};

export default BlockedModal;