import React, { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faInfo, faTrash, faExclamationTriangle } from '@fortawesome/free-solid-svg-icons';
import './DynamicIsland.css';

const LogTypes = {
  ERROR: { color: '#ffffff', icon: faExclamationTriangle },
  INFO: { color: '#ffffff', icon: null }
};

const DynamicIsland = ({ title = 'Консоль Minecraft' }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isClosing, setIsClosing] = useState(false);
  const [isPulsing, setIsPulsing] = useState(false);
  const [minecraftLogs, setMinecraftLogs] = useState([]);
  const [hasErrors, setHasErrors] = useState(false);
  const widgetRef = useRef(null);
  const islandRef = useRef(null);
  const contentRef = useRef(null);
  const logsContainerRef = useRef(null);
  
  // Максимальное количество логов для хранения
  const MAX_LOGS = 500;

  // Функция для ограничения количества логов
  const limitLogs = (logs) => {
    if (logs.length <= MAX_LOGS) return logs;
    
    // Сохраняем первый лог (важная информация о запуске) и последние (MAX_LOGS - 1) логов
    return [logs[0], ...logs.slice(-(MAX_LOGS - 1))];
  };

  useEffect(() => {
    const pulseInterval = setInterval(() => {
      if (!isOpen) {
        setIsPulsing(true);
        setTimeout(() => setIsPulsing(false), 1000);
      }
    }, 10000);

    return () => clearInterval(pulseInterval);
  }, [isOpen]);

  useEffect(() => {
    // Подписываемся только на логи от Minecraft
    if (!window.electronAPI || !window.electronAPI.onMinecraftLog) {
      return undefined;
    }
    
    const unsubscribe = window.electronAPI.onMinecraftLog((log) => {
      // Определяем тип лога - только ERROR или INFO
      let logType = "INFO";
      
      if (log.includes("Error") || log.includes("Exception") || log.includes("ОШИБКА")) {
        logType = "ERROR";
        setHasErrors(true);
      }
      
      setMinecraftLogs((prevLogs) => {
        const newLog = {
          text: log,
          type: logType,
          time: new Date().toISOString()
        };
        
        // Применяем лимит логов
        return limitLogs([...prevLogs, newLog]);
      });
      
      // Если пришел новый лог и консоль скрыта, добавляем пульсацию
      if (!isOpen) {
        setIsPulsing(true);
        setTimeout(() => setIsPulsing(false), 1000);
      }
    });

    return () => {
      if (unsubscribe) unsubscribe();
    };
  }, [isOpen]);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        isOpen &&
        widgetRef.current &&
        !widgetRef.current.contains(event.target) &&
        islandRef.current &&
        !islandRef.current.contains(event.target)
      ) {
        closeIsland();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen]);

  useEffect(() => {
    const handleContentClick = (event) => {
      if (
        contentRef.current &&
        event.target === contentRef.current
      ) {
        closeIsland();
      }
    };

    if (contentRef.current && isOpen) {
      contentRef.current.addEventListener('mousedown', handleContentClick);
    }
    
    return () => {
      if (contentRef.current) {
        contentRef.current.removeEventListener('mousedown', handleContentClick);
      }
    };
  }, [isOpen]);

  // Автоматическая прокрутка вниз при появлении новых логов
  useEffect(() => {
    if (logsContainerRef.current && isOpen && minecraftLogs.length > 0) {
      logsContainerRef.current.scrollTop = logsContainerRef.current.scrollHeight;
    }
  }, [minecraftLogs, isOpen]);

  const toggleIsland = () => {
    if (isOpen) {
      closeIsland();
    } else {
      setIsPulsing(false);
      setIsOpen(true);
    }
  };

  const closeIsland = () => {
    if (isOpen) {
      setIsClosing(true);
      setTimeout(() => {
        setIsOpen(false);
        setIsClosing(false);
      }, 200); 
    }
  };

  const handleWidgetClick = (e) => {
    e.stopPropagation();
  };

  const clearLogs = () => {
    setMinecraftLogs([]);
    setHasErrors(false);
  };
  
  const formatLogText = (log) => {
    let text = log.text;
    // Удаляем префиксы типа из текста
    text = text
      .replace(/\[MINECRAFT (DEBUG|DATA|ERROR|PROGRESS|ARGS|CLOSE)\]/g, '')
      .replace(/\[(ERROR|INFO)\]/g, '')
      .replace(/[❌✅⚠️⏳🎮📂⚙️🚀▶️]/g, ''); // Удаляем все эмодзи
    
    return text.trim();
  };

  return (
    <div className="dynamic-island-container">
      <div 
        ref={islandRef}
        className={`dynamic-island ${isPulsing && !isOpen ? 'pulse-animation' : ''} ${hasErrors ? 'has-errors' : ''}`} 
        onClick={toggleIsland}
      >
        <div className="island-collapsed">
          <FontAwesomeIcon icon={hasErrors ? faExclamationTriangle : faInfo} />
          <span>{title}</span>
          {minecraftLogs.length > 1 && (
            <span className="log-count">
              {minecraftLogs.length === MAX_LOGS ? `${MAX_LOGS}` : minecraftLogs.length}
            </span>
          )}
        </div>
      </div>

      {isOpen && (
        <div 
          ref={widgetRef}
          className={`widget-container ${isClosing ? 'widget-container-closing' : ''}`}
          onClick={handleWidgetClick}
        >
          <div className="island-header">
            <h3>{title}</h3>
            <div className="header-actions">
              <button className="clear-logs-button" onClick={clearLogs}>
                <FontAwesomeIcon icon={faTrash} /> Очистить
              </button>
            </div>
          </div>
          <div className="island-content" ref={contentRef}>
            <div className="logs-container" ref={logsContainerRef}>
              {minecraftLogs.length > 0 ? (
                minecraftLogs.map((log, index) => (
                  <div 
                    key={index} 
                    className={`log-entry log-${log.type.toLowerCase()}`}
                  >
                    <span className="log-time">
                      {new Date(log.time).toLocaleTimeString()}
                    </span>
                    <span className="log-content">
                      {LogTypes[log.type]?.icon && (
                        <FontAwesomeIcon icon={LogTypes[log.type].icon} className="log-icon" />
                      )}
                      {formatLogText(log)}
                    </span>
                  </div>
                ))
              ) : (
                <div className="log-entry log-info">
                  <span className="log-content">
                    Логи отсутствуют. Запустите Minecraft, чтобы увидеть логи здесь.
                  </span>
                </div>
              )}
            </div>
          </div>
          <div className="island-footer">
            <span>{minecraftLogs.length === MAX_LOGS ? `Отображено ${MAX_LOGS} логов (максимум)` : `Всего логов: ${minecraftLogs.length}`}</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default DynamicIsland;