import React, { useState, useEffect } from 'react';
import './Settings.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFolder, faPalette, faLanguage, faServer, faShield, faInfoCircle, faCode } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';

function Settings() {
  const [activeTab, setActiveTab] = useState('general');
  const [appVersion, setAppVersion] = useState('');
  const { isBlocked, blockUser, unblockUser, username, resetUsername } = useUser();
  
  useEffect(() => {
    if (window.electron && window.electron.ipcRenderer) {
      window.electron.ipcRenderer.once('app-version', (version) => {
        setAppVersion(version);
      });
      window.electron.ipcRenderer.sendMessage('get-app-version');
    } else {
      setAppVersion('0.0.1');
    }
  }, []);
  {/*{ id: 'security', name: 'Безопасность', icon: faShield } */}
  const tabs = [
    { id: 'general', name: 'Общие', icon: faFolder },
    { id: 'appearance', name: 'Внешний вид', icon: faPalette },
    { id: 'network', name: 'Сеть', icon: faServer },

    { id: 'about', name: 'О программе', icon: faInfoCircle },
    { id: 'developer', name: 'Для разработчиков', icon: faCode }
  ];
  
  const toggleBlockStatus = () => {
    if (isBlocked) {
      unblockUser();
    } else {
      blockUser();
    }
  };

  const handleForceLogout = () => {
    resetUsername();
  };
  


  const openDevTools = () => {
    if (window.electron && window.electron.ipcRenderer) {
      window.electron.ipcRenderer.sendMessage('open-devtools');
    } else {
      try {
        console.log('Открытие DevTools через стандартный API браузера');
        if (window.devtoolsOpen) {
          window.devtoolsOpen();
        } else {
          alert('Нажмите F12 для открытия инструментов разработчика');
        }
      } catch (error) {
        console.error('Не удалось открыть DevTools:', error);
      }
    }
  };
  
  const renderTabContent = () => {
    switch (activeTab) {
      case 'general':
        return (
          <div className="settings-section">
            <h3>Общие настройки</h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Путь к Minecraft</h4>
                <p>Укажите путь к папке с игрой</p>
              </div>
              <div className="setting-control">
                <input type="text" className="path-input" value="C:\Users\Username\AppData\Roaming\.minecraft" readOnly />
                <button className="browse-button">Обзор</button>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Автозапуск</h4>
                <p>Запускать лаунчер при старте Windows</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
            </div>
        );
      case 'appearance':
        return (
          <div className="settings-section">
            <h3>Внешний вид</h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Тема</h4>
                <p>Выберите тему оформления</p>
              </div>
              <div className="setting-control">
                <select className="theme-select">
                  <option value="light">Светлая</option>
                  <option value="dark">Темная</option>
                  <option value="system">Системная</option>
                </select>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Размер шрифта</h4>
                <p>Настройте размер текста в интерфейсе</p>
              </div>
              <div className="setting-control">
                <select className="font-size-select">
                  <option value="small">Маленький</option>
                  <option value="medium" selected>Средний</option>
                  <option value="large">Большой</option>
                </select>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Задний фон</h4>
                <p>Выберите изображение заднего фона</p>
              </div>  
              <div className="setting-control">
                <button className="background-button">Выбрать изображение</button>
              </div>
            </div>
          </div>
        );
      case 'network':
        return (
          <div className="settings-section">
            <h3>Сеть</h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Дополнительный айпи</h4>
                <p>Для подключения без ограничений. (Актуально для игроков из Украины)</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'security':
        return (
          <div className="settings-section">
            <h3>Безопасность</h3>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Двухфакторная аутентификация</h4>
                <p>Дополнительный уровень защиты аккаунта</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
            <div className="setting-item">
              <div className="setting-info">
                <h4>Сохранение пароля</h4>
                <p>Автоматически сохранять пароль для входа</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" defaultChecked />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'developer':
        return (
          <div className="settings-section">
            <h3>Инструменты разработчика</h3>
            <div className="dev-warning">
              <FontAwesomeIcon icon={faCode} className="warning-icon" />
              <p>Не лазай тут, дэбил</p>
            </div>
            
            {username && (
              <>
                <div className="setting-item">
                  <div className="setting-info">
                    <h4>Управление блокировкой</h4>
                    <p>Тестирование функционала блокировки аккаунта</p>
                  </div>
                  <div className="setting-control">
                    <button 
                      className={`block-toggle-button ${isBlocked ? 'blocked' : 'active'}`}
                      onClick={toggleBlockStatus}
                    >
                      {isBlocked ? 'Разблокировать' : 'Заблокировать'}
                    </button>
                  </div>
                </div>
                
                <div className="setting-item">
                  <div className="setting-info">
                    <h4>Принудительный выход</h4>
                    <p>Сброс данных пользователя и переход к экрану входа</p>
                  </div>
                  <div className="setting-control">
                    <button 
                      className="dev-button"
                      onClick={handleForceLogout}
                    >
                      Сбросить профиль
                    </button>
                  </div>
                </div>
              </>
            )}
            
            <div className="setting-item">
              <div className="setting-info">
                <h4>Режим отладки</h4>
                <p>Включить расширенное логирование</p>
              </div>
              <div className="setting-control">
                <label className="toggle-switch">
                  <input type="checkbox" />
                  <span className="toggle-slider"></span>
                </label>
              </div>
            </div>
          </div>
        );
      case 'about':
        return (
          <div className="settings-section">
            <h3>О программе</h3>
            <div className="about-info">
              <div className="app-logo">
                <img src="https://via.placeholder.com/100" alt="CMLauncher" />
              </div>
              <div className="app-details">
                <h2>CMLauncher</h2>
                <p className="version">Версия {appVersion}</p>
                <p className="description">
                  CMLauncher - современный лаунчер для Minecraft с поддержкой модпаков,
                  кастомизации и удобным интерфейсом.
                </p>
                <div className="app-links">
                  <a href="https://cmlauncher.ru" className="app-link" target="_blank" rel="noopener noreferrer">Официальный сайт</a>
                </div>
                <div className="creator-info">
                  <img src="https://via.placeholder.com/40" alt="creator" className="creator-avatar" />
                  <div className="creator-details">
                    <p className="creator">Made by Revellison</p>
                    <a href="https://revellison.ru" className="app-link" target="_blank" rel="noopener noreferrer">revellison.ru</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        );
      default:
        return null;
    }
  };
  
  return (
    <div className="settings-page">
      <div className="settings-sidebar">
        <h2>Настройки</h2>
        <div className="settings-tabs">
          {tabs.map(tab => (
            <button 
              key={tab.id}
              className={`settings-tab ${activeTab === tab.id ? 'active' : ''}`}
              onClick={() => setActiveTab(tab.id)}
              data-tab={tab.id}
            >
              <FontAwesomeIcon icon={tab.icon} />
              <span>{tab.name}</span>
            </button>
          ))}
        </div>
      </div>
      <div className="settings-content">
        {renderTabContent()}
      </div>
    </div>
  );
}

export default Settings;