import React, { useState, useEffect } from 'react';
import './Header.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCube, faPenRuler, faGear, faRotate, faHome, faSearch, faBell, faChevronDown } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';
import avatarPlaceholder from '../../../../assets/avatar-placeholder.png';
import logo from '../../../../assets/logo.png';
import DynamicIsland from '../DynamicIsland/DynamicIsland';

function Header() {
  const { username, resetUsername } = useUser();
  const [searchQuery, setSearchQuery] = useState('');
  const [notifications, setNotifications] = useState([
    { id: 1, text: 'Доступно обновление лаунчера', time: '5 мин назад', read: false },
    { id: 2, text: 'Новый модпак добавлен', time: '1 час назад', read: false }
  ]);
  const [showNotifications, setShowNotifications] = useState(false);
  const [showProfileMenu, setShowProfileMenu] = useState(false);

  const handleSearch = (e) => {
    setSearchQuery(e.target.value);
  };

  const toggleNotifications = () => {
    setShowNotifications(!showNotifications);
    setShowProfileMenu(false);
  };

  const toggleProfileMenu = () => {
    setShowProfileMenu(!showProfileMenu);
    setShowNotifications(false);
  };

  const markNotificationAsRead = (id) => {
    setNotifications(notifications.map(notification => 
      notification.id === id ? { ...notification, read: true } : notification
    ));
  };


  const handleLogout = () => {
    resetUsername(); 
    setShowProfileMenu(false);
  };

  const unreadCount = notifications.filter(n => !n.read).length;

  return (
    <div className="header">
      <div className="header-left">
      </div>
      <div className="header-center">
        <DynamicIsland title="Консоль Minecraft" />
      </div>
      <div className="header-right">
        <div className="header-actions">
          <div className="notification-container">
            <button className="icon-button" onClick={toggleNotifications}>
              <FontAwesomeIcon icon={faBell} />
              {unreadCount > 0 && <span className="notification-badge">{unreadCount}</span>}
            </button>
            {showNotifications && (
              <div className="notifications-dropdown">
                <div className="notifications-header">
                  <h3>Уведомления</h3>
                  {unreadCount > 0 && (
                    <button className="mark-all-read">Прочитать все</button>
                  )}
                </div>
                <div className="notifications-list">
                  {notifications.length > 0 ? (
                    notifications.map(notification => (
                      <div 
                        key={notification.id} 
                        className={`notification-item ${!notification.read ? 'unread' : ''}`}
                        onClick={() => markNotificationAsRead(notification.id)}
                      >
                        <div className="notification-content">
                          <p>{notification.text}</p>
                          <span className="notification-time">{notification.time}</span>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="no-notifications">
                      <p>Нет новых уведомлений</p>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
          <div className="profile-container">
            <div className="profile" onClick={toggleProfileMenu}>
              <div className="profile-avatar">
                <img src={avatarPlaceholder} alt="Профиль" />
              </div>
              <div className="profile-info">
                <span className="profile-name">{username || 'Гость'}</span>
                <FontAwesomeIcon icon={faChevronDown} className="profile-settings-icon" />
              </div>
            </div>
            {showProfileMenu && (
              <div className="profile-dropdown">
                <div className="profile-dropdown-header">
                  <div className="profile-dropdown-avatar">
                    <img src={avatarPlaceholder} alt="Профиль" />
                  </div>
                  <div className="profile-dropdown-info">
                    <h3>{username || 'Гость'}</h3>
                    <p>Игрок</p>
                  </div>
                </div>
                <div className="profile-dropdown-menu">
                  {/*<button className="profile-menu-item">
                    <FontAwesomeIcon icon={faHome} />
                    <span>Мой профиль</span>
                  </button>
                        <button className="profile-menu-item">
                    <FontAwesomeIcon icon={faGear} />
                    <span>Настройки</span>
                  </button>*/}
                  <button className="profile-menu-item" onClick={handleLogout}>
                    <FontAwesomeIcon icon={faRotate} />
                    <span>Выйти</span>
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;