import React, { useState, useEffect } from 'react';
import './Home.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCube, faPlay } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';
import avatarPlaceholder from '../../../../assets/avatar-placeholder.png';

function Home() {
  const { username } = useUser();

  const [userData, setUserData] = useState({
    discordTag: 'user#1234',
    playTime: '150 часов',
    status: 'Игрок'
  });
  
  const [currentModpack, setCurrentModpack] = useState({
    name: 'Nickly Optimized',
    description: 'Оптимизированная сборка с упором на производительность и стабильность игры. Идеально подходит для слабых компьютеров.',
    version: '1.21.5',
    isInstalled: true
  });
  
  const [isLaunching, setIsLaunching] = useState(false);
  const [launchError, setLaunchError] = useState('');
  
  const handlePlayClick = async () => {
    if (!username) {
      alert('Пожалуйста, введите ваш никнейм в настройках профиля');
      return;
    }

    setIsLaunching(true);
    setLaunchError('');
    
    try {
      const usernameStr = String(username);
      console.log(`Запуск игры для пользователя: ${usernameStr} с модпаком: ${currentModpack.name}`);
      const result = await window.electronAPI.launchGame(usernameStr);
      
      if (result.success) {
        console.log(`Minecraft успешно запущен (PID: ${result.pid})`);
      } else {
        console.error('Ошибка при запуске:', result.error);
        setLaunchError(result.error);
      }
    } catch (error) {
      console.error('Ошибка при запуске игры:', error);
      setLaunchError('Не удалось запустить игру. Попробуйте позже.');
    } finally {
      setIsLaunching(false);
    }
  };
  
  return (
    <div className="home-page">
      <div className="content-center-vertical">
        <div className="main-widget">
          <div className="player-section">
            <div className="profile-card">
              <h2>{username || 'Гость'}</h2>
              <img 
                src={avatarPlaceholder} 
                alt="Аватар" 
                loading="eager"
                draggable="false"
              />
              <div className="profile-info-main"> 
                <p><span>Статус:</span> {userData.status}</p>
                <p><span>Всего наиграно:</span> {userData.playTime}</p>
                <p><span>Discord:</span> <span style={{ userSelect: 'text' }}>{userData.discordTag}</span></p>
              </div>
            </div>
            
            <div className="modpack-card-home">
              <h2>Текущий модпак</h2>
              <div className="current-modpack">
                <div className="modpack-header">
                  <div className="modpack-icon">
                    <FontAwesomeIcon icon={faCube} />
                  </div>
                  <div className="modpack-title">
                    <h3>{currentModpack.name}</h3>
                    <div className={`modpack-status ${currentModpack.isInstalled ? 'installed' : ''}`}>
                      {currentModpack.isInstalled ? 'Установлен' : 'Не установлен'}
                    </div>
                  </div>
                </div>
                
                <div className="modpack-description">
                  {currentModpack.description}
                </div>
                
                {launchError && <div className="launch-error">{launchError}</div>}
                
                <button className="play-button" onClick={handlePlayClick} disabled={isLaunching}>
                  <FontAwesomeIcon icon={faPlay} />
                  <span>{isLaunching ? 'Запуск...' : 'Играть'}</span>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;