import React, { useState, useEffect } from 'react';
import { useUser } from '../../../context/UserContext';
import './HelloModal.css';

const HelloModal = ({ onClose }) => {
  const { username, updateUsername } = useUser();
  const [inputValue, setInputValue] = useState(username || '');
  const [error, setError] = useState('');
  const [isVisible, setIsVisible] = useState(true);
  const [isReLogin, setIsReLogin] = useState(false);

  useEffect(() => {
    const previousUsername = localStorage.getItem('username');
    if (previousUsername && !username) {
      setIsReLogin(true);
      setInputValue(previousUsername);
    }
  }, [username]);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!inputValue.trim()) {
      setError('Пожалуйста, введите никнейм');
      return;
    }
    
    if (inputValue.length < 3) {
      setError('Никнейм должен содержать минимум 3 символа');
      return;
    }
    
    if (inputValue.length > 16) {
      setError('Никнейм не должен превышать 16 символов');
      return;
    }

    if (/[а-яА-ЯёЁ]/.test(inputValue)) {
      setError('Никнейм не должен содержать киррилицу');
      return;
    }
    
    const trimmedUsername = inputValue.trim();
    updateUsername(trimmedUsername);

    setIsVisible(false);
    setTimeout(() => {
      if (onClose) onClose();
    }, 300); 
  };

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
    if (error) setError('');
  };

  return (
    <div className={`hello-modal-overlay ${isVisible ? 'visible' : 'hidden'}`}>
      <div className="hello-modal">
        <div className="hello-modal-content">
          <h2>{isReLogin ? 'С возвращением!' : 'Добро пожаловать!'}</h2>
          <p>
            {isReLogin 
              ? 'Пожалуйста, введите ваш никнейм для входа:' 
              : 'Пожалуйста, введите ваш никнейм для игры на наших серверах:'}
          </p>
          
          <form onSubmit={handleSubmit}>
            <div className="input-group">
              <input
                type="text"
                value={inputValue}
                onChange={handleInputChange}
                placeholder="Ваш никнейм"
                className={error ? 'error' : ''}
                autoFocus
              />
              {error && <div className="error-message">{error}</div>}
            </div>
            
            <button type="submit" className="submit-button">
              {isReLogin ? 'Войти' : 'Сохранить'}
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default HelloModal;
