import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTools, faHardHat, faCog } from '@fortawesome/free-solid-svg-icons';
import './UnderDevelopment.css';

const UnderDevelopment = () => {
  return (
    <div className="under-development-page">
      <div className="construction-container">
        <div className="icon-container">
          <FontAwesomeIcon icon={faHardHat} className="construction-icon main-icon" />
          <FontAwesomeIcon icon={faTools} className="construction-icon tools-icon" />
          <FontAwesomeIcon icon={faCog} className="construction-icon cog-icon spinning" />
        </div>
        
        <h1>Страница в разработке</h1>
        
        <p className="development-message">
          Мы усердно работаем над улучшением этого раздела.
          <br />
          Скоро здесь появится новый функционал!
        </p>
        
        <div className="progress-container">
          <div className="progress-bar">
            <div className="progress-indicator"></div>
          </div>
          <p className="progress-text">Работа в процессе...</p>
        </div>
      </div>
    </div>
  );
};

export default UnderDevelopment; 