// src/pages/Modpacks/Modpacks.jsx
import React, { useState, useEffect } from 'react';
import './Modpacks.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDownload, faUser, faClock } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';
import modpacksData from '../../data/modpacks.json';

import nicklyOptimizedImage from '../../data/modpacks/nickly_optimized.png';
import nicklyVisualizedImage from '../../data/modpacks/nickly_visualized.png';
import nicklyOmgImage from '../../data/modpacks/nickly_omg.png';

const imageMap = {
  './modpacks/nickly_optimized.png': nicklyOptimizedImage,
  './modpacks/nickly_visualized.png': nicklyVisualizedImage,
  './modpacks/nickly_omg.png': nicklyOmgImage
};

const STORAGE_KEY = 'cmLauncher_modpacks';

function Modpacks() {
  const { username } = useUser();
  const [modpacks, setModpacks] = useState([]);
  const [isInstalling, setIsInstalling] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadModpacks = async () => {
      try {
        const timestamp = new Date().getTime();

        if (require.cache[require.resolve('../../data/modpacks.json')]) {
          delete require.cache[require.resolve('../../data/modpacks.json')];
        }
        
        const freshModpacks = require('../../data/modpacks.json');
        
        localStorage.setItem(STORAGE_KEY, JSON.stringify({
          timestamp: timestamp,
          modpacks: freshModpacks
        }));
        
        setModpacks(freshModpacks);
        setLoading(false);
      } catch (error) {
        console.error('Ошибка при загрузке модпаков:', error);
        
        const storedData = localStorage.getItem(STORAGE_KEY);
        
        if (storedData) {
          try {
            const parsedData = JSON.parse(storedData);
            setModpacks(parsedData.modpacks);
          } catch (parseError) {
            setModpacks(modpacksData);
          }
        } else {
          setModpacks(modpacksData);
        }
        
        setLoading(false);
      }
    };

    loadModpacks();
  }, []);

  const handleInstall = async (modpackId) => {
    setIsInstalling(modpackId);
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      console.log(`Установка модпака с ID: ${modpackId}`);
    } catch (error) {
      console.error('Ошибка установки модпака:', error);
    } finally {
      setIsInstalling(null);
    }
  };

  const handleCreatorClick = (e, url) => {
    e.preventDefault();
    if (window.electronAPI && window.electronAPI.openExternal) {
      window.electronAPI.openExternal(url);
    } else {
      window.open(url, '_blank', 'noopener,noreferrer');
    }
  };

  if (loading) {
    return <div className="modpacks-loading">Загрузка модпаков...</div>;
  }

  return (
    <div className="modpacks-page">      
      <div className="modpacks-grid">
        {modpacks.map(modpack => (
          <div key={modpack.id} className="modpack-card">
            <div className="modpack-image">
              <img src={imageMap[modpack.imageUrl]} alt={modpack.name} />
              <div className="modpack-version">{modpack.version}</div>
            </div>
            <div className="modpack-content">
              <h3>{modpack.name}</h3>
              <p>{modpack.description}</p>
              <div className="modpack-stats">
                <div className="stat">
                  <FontAwesomeIcon icon={faUser} />
                  <span><a href={modpack.creatorLink} className="creator-link" onClick={(e) => handleCreatorClick(e, modpack.creatorLink)}>{modpack.creator}</a></span>
                </div>
                <div className="stat">
                  <FontAwesomeIcon icon={faClock} />
                  <span>{modpack.lastUpdated}</span>
                </div>
              </div>
              <button 
                className={`install-button ${isInstalling === modpack.id ? 'installing' : ''}`}
                onClick={() => handleInstall(modpack.id)}
                disabled={isInstalling !== null}
              >
                <FontAwesomeIcon icon={faDownload} />
                <span>
                  {isInstalling === modpack.id ? 'Установка...' : 'Установить'}
                </span>
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default Modpacks;