import { MemoryRouter as Router, Routes, Route, useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';
import './App.css';

import { UserProvider, useUser } from './context/UserContext';

import HelloModal from './components/Modals/HelloModal/HelloModal';
import BlockedModal from './components/Modals/BlockedModal/BlockedModal';

// маршруты
import Home from './pages/Home/Home';
import Modpacks from './pages/Modpacks/Modpacks';
import Customization from './pages/Customization/Customization';
import Settings from './pages/Settings/Settings';
import Map from './pages/Map/Map';
import UnderDevelopment from './pages/UnderDevelopment/UnderDevelopment';
import Sidebar from './components/Sidebar/Sidebar.jsx';
import Header from './components/Header/Header.jsx';
// import Titlebar from './components/TitleBar/TitleBar.jsx'; 
import Tooltip from './components/Tooltip/Tooltip.jsx';
import Footer from './components/Footer/Footer.jsx';

function MainAppContent() {
  const { username, isBlocked } = useUser();
  const [showHelloModal, setShowHelloModal] = useState(false);
  const [showBlockedModal, setShowBlockedModal] = useState(false);
  const location = useLocation();
  const isMapPage = location.pathname === '/map';

  useEffect(() => {
    if (!username) {
      setShowHelloModal(true);
      setShowBlockedModal(false);
    } 
    else if (isBlocked) {
      setShowBlockedModal(true);
      setShowHelloModal(false);
    } 
    else {
      setShowHelloModal(false);
      setShowBlockedModal(false);
    }
  }, [username, isBlocked]);

  return (
    <>
      <div className="app-container">
        <Sidebar isMapPage={isMapPage} />
        <div className={`main-content ${isMapPage ? 'map-page' : ''}`}>
          <Header />
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/modpacks" element={<Modpacks />} />
            <Route path="/customization" element={<UnderDevelopment />} />
            <Route path="/map" element={<Map />} />
            <Route path="/settings" element={<Settings />} />
          </Routes>
        </div>
      </div>
      <Footer isMapPage={isMapPage} />
      <Tooltip />

      {showHelloModal && (
        <HelloModal onClose={() => setShowHelloModal(false)} />
      )}
      {showBlockedModal && (
        <BlockedModal onClose={() => setShowBlockedModal(false)} />
      )}
    </>
  );
}

function MainApp() {
  return (
    <Router>
      {/* <Titlebar /> */}
      <MainAppContent />
    </Router>
  );
}

export default function App() {
  return (
    <UserProvider>
      <MainApp />
    </UserProvider>
  );
}
