import React, { useState, useRef, useEffect } from 'react';
import './Footer.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faFolder, faGears, faChevronDown } from '@fortawesome/free-solid-svg-icons';
import { useUser } from '../../context/UserContext';

function Footer({ isMapPage }) {
  const [isVersionDropdownOpen, setIsVersionDropdownOpen] = useState(false);
  const [selectedVersion, setSelectedVersion] = useState('Основной');
  const [isLaunching, setIsLaunching] = useState(false);
  const [launchError, setLaunchError] = useState('');
  const dropdownRef = useRef(null);
  const { username } = useUser();

  const versions = [
    { id: 'main', name: 'Основной' },
    { id: 'event', name: 'Ивентовый' }
  ];

  const handlePlayGame = async () => {
    if (!username) {
      alert('Пожалуйста, введите ваш никнейм в настройках профиля');
      return;
    }

    setIsLaunching(true);
    setLaunchError('');
    
    try {
      const usernameStr = String(username);
      console.log(`Запуск игры для пользователя: ${usernameStr}`);
      const result = await window.electronAPI.launchGame(usernameStr);
      
      if (result.success) {
        console.log(`Minecraft успешно запущен (PID: ${result.pid})`);
      } else {
        console.error('Ошибка при запуске:', result.error);
        setLaunchError(result.error);
      }
    } catch (error) {
      console.error('Ошибка при запуске игры:', error);
      setLaunchError('Не удалось запустить игру. Попробуйте позже.');
    } finally {
      setIsLaunching(false);
    }
  };

  const handleOpenFolder = () => {
    console.log('Открытие директории');
  };

  const toggleVersionDropdown = () => {
    setIsVersionDropdownOpen(!isVersionDropdownOpen);
  };

  const selectVersion = (version) => {
    setSelectedVersion(version.name);
    setIsVersionDropdownOpen(false);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setIsVersionDropdownOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <div className={`footer ${isMapPage ? 'map-mode' : ''}`}>
      <div className="footer-left">
        <div className="version-info">
          {/*<div className={`version-dropdown ${isVersionDropdownOpen ? 'open' : ''}`} ref={dropdownRef}>
            <div className="version-selector" onClick={toggleVersionDropdown}>
              <span className="mc-version">{selectedVersion}</span>
              <FontAwesomeIcon icon={faChevronDown} className="dropdown-icon" />
            </div>
            {isVersionDropdownOpen && (
              <div className="version-dropdown-menu">
                {versions.map((version) => (
                  <div 
                    key={version.id} 
                    className={`version-option ${selectedVersion === version.name ? 'selected' : ''}`}
                    onClick={() => selectVersion(version)}
                  >
                    {version.name}
                  </div>
                ))}
              </div>
            )}
          </div>*/}
          <span className="server-status online">Сервер онлайн</span>
          {launchError && <span className="error-message">{launchError}</span>}
        </div>
      </div>

      <div className="footer-center">
        <button className="play-button" onClick={handlePlayGame} disabled={isLaunching}>
          <FontAwesomeIcon icon={faPlay} />
          <span>{isLaunching ? 'Запуск...' : 'Играть'}</span>
        </button>
      </div>

      <div className="footer-right">
        <button className="folder-button">
          <FontAwesomeIcon icon={faFolder} />
          <span>Открыть директорию</span>
        </button>
        
        <div className="footer-actions">
          <button className="footer-icon-button" title="Настройки запуска">
            <FontAwesomeIcon icon={faGears} />
          </button>
        </div>
      </div>
    </div>
  );
}

export default Footer;