import './Map.css';
import React, { useEffect } from 'react';

function Map() {
  useEffect(() => {
    document.title = 'CMLauncher - Карта';
    
    return () => {
      document.title = 'CMLauncher';
    };
  }, []);
  
  return (
    <div className="map-container">
      <iframe 
        src="https://cmcraft.su/map/" 
        width="100%" 
        height="100%" 
        allowFullScreen={true}
        loading="lazy" 
        referrerPolicy="no-referrer-when-downgrade"
        title="Карта CMCraft"
      ></iframe>
    </div>
  );
}

export default Map;